		</Mineheard>
		<Minejichuxinxi text="基础信息"></Minejichuxinxi>
		<Minetouxiang text="头像">
			<img src="../../assets/images/0.jpg" />
		</Minetouxiang>
		<Minenicheng text="昵称">			
			<div class="fuji">
				<input type="text" placeholder=" 请输入昵称" @click="fn($event)" :class='{"red":isRed}' @blur="fn2()" @keyup="fa1($event)"></input>
				<Minezhengze v-show="showImg"></Minezhengze>
			</div>
			
		</Minenicheng>

		<Minenicheng text="会员号">
			<input type="text" placeholder=" 请输入会员号" @click="fn6()" :class='{"yellow":issRed}' @blur="fn7()" ></input>
		</Minenicheng>
		<Minenicheng text="邮箱地址">
			<input type="text" placeholder=" 请输入邮箱地址" @click="fn8()" :class='{"black":isssRed}' @blur="fn9()" ></input>
		</Minenicheng>
		<Minenicheng text="性别">
			<div class="fuji">
				<input type="text" placeholder=" 请输入性别" @click="fn4($event)" :class='{"blue":iRed}' @blur="fn5()"  @keyup="fa1($event)"></input>
				<Minezhengze v-show="showImg"></Minezhengze>
			</div>
			
		</Minenicheng>
		<Minenicheng text="生日">
			
		    <span class="nian">日</span>
		    <select name="select" id="select_k1" class="xla_k">
			    <option value="请选择">请选择</option>
				<option value="27">27</option>
				<option value="28">28</option>
				<option value="29">29</option>
				<option value="30">30</option>
				<option value="31">31</option>
		    </select>
		    <span class="nian">月</span>
		    <select name="select" id="select_k1" class="xla_k">
			    <option value="请选择">请选择</option>
				<option value="8">8</option>
				<option value="9">9</option>
				<option value="10">10</option>
				<option value="11">11</option>
				<option value="12">12</option>
		    </select>
		    <span class="nian">年</span>
		    <select name="select" id="select_k1" class="xla_k">
			    <option value="请选择">请选择</option>
				<option value="2013">2013</option>
				<option value="2014">2014</option>
				<option value="2015">2015</option>
				<option value="2016">2016</option>
				<option value="2017">2017</option>
		    </select>
		</Minenicheng>
		<Minetouxiang text="我的地址" class="dizhi">
			<div class="xqwen">
				<span class="xiaozi">张三 13551545555</span>
				<span class="xiaozi">四川省成都市金牛区 西</span>
				<span class="xiaozi">四川大学</span>
			</div>			
		</Minetouxiang>
		<Mineyinghangxinxi text="银行信息"></Mineyinghangxinxi>
		<Minenicheng text="银行收款">
			<select name="select" id="select_k1" class="xla_y">
			    <option value="请选择">请选择</option>
				<option value="工商银行">工商银行</option>
				<option value="农业银行">农业银行</option>
				<option value="中国银行">中国银行</option>
				<option value="建设银行">建设银行</option>
				<option value="花旗银行">花旗银行</option>
		    </select>
		</Minenicheng>
		<Minenicheng text="所属省份">
			<select name="select" id="select_k1" class="xla_y">
			    <option value="省份">省份</option>
				<option value="四川省">四川省</option>
				<option value="重庆市">重庆市</option>
				<option value="重庆市">重庆市</option>
				<option value="北京市">北京市</option>
				<option value="北京市">北京市</option>
		    </select>
		</Minenicheng>
		<Minenicheng text="所属市">
			<select name="select" id="select_k1" class="xla_y">
			    <option value="城市">城市</option>
				<option value="成都市">成都市</option>
				<option value="巴中市">巴中市</option>
				<option value="深圳市">深圳市</option>
				<option value="哈尔滨市">哈尔滨市</option>
				<option value="黑市市">黑市市</option>
		    </select>
