		<Minenicheng text="所属支行">
			<input type="text" placeholder=" 请输入支行名称" @click="fn10()" :class='{"lmka":issssRed}' @blur="fn11()" ></input>
		</Minenicheng>
		<Minenicheng text="开户行">
			<input type="text" placeholder=" 请输入开户行" @click="fn12()" :class='{"lmkb":isssssRed}' @blur="fn13()" ></input>
		</Minenicheng>
		<Minenicheng text="银行账号">
			<input type="text" placeholder=" 请输入银行账号" @click="fn14()" :class='{"lmkc":issssssRed}' @blur="fn15()" ></input>
		</Minenicheng>
		<Minebaocun text="保存"></Minebaocun>
	</div>
</template>
<script>
	import Mineheard from '../../components/lmk_mine_heard.vue'
	import Minejichuxinxi from '../../components/lmk_min_jichuxinxi.vue'
	import Minetouxiang from '../../components/lmk_mine_touxiang.vue'
	import Minenicheng from '../../components/lmk_mine_nicheng.vue'
	import Mineyinghangxinxi from '../../components/lmk_mine_yinghangxinxi.vue'
	import Minebaocun from '../../components/lmk_mine_baocun.vue'
	import Minezhengze from '../../components/lmk_mine_zhengze.vue'
	export default {
		components:{
			Mineheard,
			Minejichuxinxi,
			Minetouxiang,
			Minenicheng,
			Mineyinghangxinxi,
			Minebaocun,
			Minezhengze
		},
		data:function () {
			// body...
			return{
				isRed:false,
				issRed:false,
				isssRed:false,
				issssRed:false,
				isssssRed:false,
				issssssRed:false,
				iRed:false,
				showImg:false
			}
		},
		methods:{
			fn:function (e) {
				// body...
				this.isRed=true
				var reg = /[^\u4E00-\u9FA5]/g;
				var str = e.target.value;
				if(str.match(reg)){
					this.showImg=true
				}

			},
			fn2:function () {
				// body...
				this.isRed=false
				this.showImg=false
			},
			fn4:function (e) {
				// body...
				this.iRed=true
				var reg = /[^\u4E00-\u9FA5]/g;
				var str = e.target.value;
				if(str.match(reg)){
					this.showImg=true
				}	
			},
			fn5:function () {
				// body...
				this.iRed=false
				this.showImg=false
			},
			fn6:function () {
				// body...
				this.issRed=true	
			},
			fn7:function () {
				// body...
				this.issRed=false
			},
			fn8:function () {
				// body...
				this.isssRed=true	
			},
			fn9:function () {
				// body...
				this.isssRed=false
			},
			fn10:function () {
				// body...
				this.issssRed=true	
			},
			fn11:function () {
				// body...
				this.issssRed=false
			},
			fn12:function () {
				// body...
				this.isssssRed=true	
			},
			fn13:function () {
				// body...
				this.isssssRed=false
			},
			fn14:function () {
				// body...
				this.issssssRed=true	
			},
			fn15:function () {
				// body...
				this.issssssRed=false
			},


			fa1:function(e){
				var reg = /[^\u4E00-\u9FA5]/g;
				var str = e.target.value;
				if(str.match(reg)){
					this.showImg=true
				}
			}
		}
	}
</script>
<style type="text/css">
	
	.red{background-color: rgba(0,0,0,.1);}
	.blue{background-color: rgba(0,0,0,.1);}
	.yellow{background-color: rgba(0,0,0,.1);}
	.black{background-color: rgba(0,0,0,.1);}
	.lmka{background-color: rgba(0,0,0,.1);}
	.lmkb{background-color: rgba(0,0,0,.1);}
	.lmkc{background-color: rgba(0,0,0,.1);}
	.fuji{position: relative;}
</style>


